.bytemd {
  height: calc(100vh - 64px - 40px) !important;
}
.markdown-body pre code {
  border-radius: 0.3em;
  padding-left: 15px;
}

.markdown-body {
  word-break: break-word;
  line-height: 1.75;
  font-weight: 400;
  font-size: 15px;
  overflow-x: hidden;
  color: #333;
}

.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  line-height: 1.5;
  margin-top: 35px;
  margin-bottom: 10px;
  padding-bottom: 5px;
}

.markdown-body h1 {
  font-size: 30px;
  margin-bottom: 5px;
}

.markdown-body h2 {
  padding-bottom: 12px;
  font-size: 24px;
  border-bottom: 1px solid #ececec;
}

.markdown-body h3 {
  font-size: 18px;
  padding-bottom: 0;
}

.markdown-body h4 {
  font-size: 16px;
}

.markdown-body h5 {
  font-size: 15px;
}

.markdown-body h6 {
  margin-top: 5px;
}

.markdown-body p {
  line-height: inherit;
  margin-top: 22px;
  margin-bottom: 22px;
}

.markdown-body img {
  max-width: 100%;
}

.markdown-body hr {
  border: none;
  border-top: 1px solid #ddd;
  margin-top: 32px;
  margin-bottom: 32px;
}

.markdown-body p > code {
  word-break: break-word;
  border-radius: 2px;
  overflow-x: auto;
  background-color: #fff5f5;
  color: #ff502c;
  /* font-size: 0.87em; */
  /* padding: 0.065em 0.4em; */
}

.markdown-body a {
  text-decoration: none;
  color: #0269c8;
  border-bottom: 1px solid #d1e9ff;
}

.markdown-body a:active,
.markdown-body a:hover {
  color: #275b8c;
}

.markdown-body .table-responsive {
  width: 290px;
  overflow-x: auto;
  min-height: 0.01%;
}
.markdown-body table {
  display: inline-block !important;
  font-size: 12px;
  width: auto;
  max-width: 100%;
  overflow: auto;
  border: 1px solid #f6f6f6;
}

.markdown-body thead {
  background: #f6f6f6;
  color: #000;
  text-align: left;
  white-space: nowrap;
}

.markdown-body tr:nth-child(2n) {
  background-color: #fcfcfc;
}

.markdown-body td,
.markdown-body th {
  padding: 12px 7px;
  line-height: 24px;
  border: 1px solid #eee;
}

.markdown-body td {
  /* // min-width: 120px */
}

.markdown-body blockquote {
  color: #666;
  padding: 1px 23px;
  margin: 22px 0;
  border-left: 4px solid #cbcbcb;
  background-color: #f8f8f8;
}

.markdown-body blockquote:after {
  display: block;
  content: '';
}

.markdown-body blockquote > p {
  margin: 10px 0;
}

.markdown-body ol,
.markdown-body ul {
  /* padding-left: 28px */
}

.markdown-body ol li,
.markdown-body ul li {
  margin-bottom: 0;
  list-style: inherit;
}

.markdown-body ol li .task-list-item,
.markdown-body ul li .task-list-item {
  list-style: none;
}

.markdown-body ol li .task-list-item ol,
.markdown-body ol li .task-list-item ul,
.markdown-body ul li .task-list-item ol,
.markdown-body ul li .task-list-item ul {
  margin-top: 0;
}

.markdown-body ol ol,
.markdown-body ol ul,
.markdown-body ul ol,
.markdown-body ul ul {
  margin-top: 3px;
}

.markdown-body ol li {
  padding-left: 6px;
}

@media (max-width: 720px) {
  .markdown-body h1 {
    font-size: 24px;
  }

  .markdown-body h2 {
    font-size: 20px;
  }

  .markdown-body h3 {
    font-size: 18px;
  }
}
